.container-select {
  position: relative;
  /* display: block; */
  /* background-color: green; */
  /* width: 100px; */
  display: initial;
  cursor: pointer !important;
}

.copy-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  cursor: pointer !important;
  margin: 0.5rem;
  font-family: Arial, Helvetica, sans-serif, bold;
  font-weight: bold;
  background-color: #00569d;
  color: white;
}

.copy-button:hover {
  /* background-color: rgba(128, 128, 128, 0.087); */
  background-color: #4d89ba;
}

.select-dropdown2 {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  padding: 0;
  margin: 0;
  font-size: 1em;
  display: block;
  position: absolute;
  /* background-color: red; */
  width: 100% !important;
  left: 0;
  top: 0;
  color: transparent;
  cursor: pointer;
}

.select-dropdown {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  padding: 0;
  margin: 0;
  font-size: 1em;
  width: 100%;
}

.input-title,
.input-title:focus-visible,
.input-title:active {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  outline: none !important;
  font-size: medium;
  min-width: 1px;
}

.select-dropdown::-ms-expand {
  display: none;
}

.hidden {
  opacity: 0;
  position: absolute;
  right: 0;
}

.date-picker {
  opacity: 0;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}

.container-select {

  input[type="date"]::-webkit-datetime-edit,
  input[type="date"]::-webkit-inner-spin-button,
  input[type="date"]::-webkit-clear-button {
    color: red;
    position: relative;
    width: 0 !important;
  }

  input::-webkit-calendar-picker-indicator {
    width: 100% !important;
    cursor: pointer;
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  width: 100vw;
  flex-direction: column;
}

.container-select:hover,
.input-title:hover,
.container-select:hover,
.select-dropdown2:hover,
.container-select:focus-visible,
.input-title:focus-visible,
.container-select:focus-visible,
.select-dropdown2:focus-visible {
  /* outline: 1px gray solid !important; */
  background-color: #00569d5d;
  background-color: #4d89ba;
  color: white;
  border-radius: 0.4rem;
}

.title {
  padding-bottom: 3rem;
}

/* * {
  color: #fff;
  background-color: black;
} */

* {
  /* font-family: Courier; */
  font-size: medium;
}

.unset {
  color: dimgray;
}

.autocomplete-suggestions {
  position: absolute;
  top: 100%; /* Position the dropdown below the input-title span */
  left: 0;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  border: 1px solid rgba(0, 0, 0, 0.1); /* Light border */
  max-height: 150px;
  overflow-y: auto;
  background-color: rgba(255, 255, 255, 0.9); /* Slightly transparent background */
  z-index: 1000;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  border-radius: 4px; /* Rounded corners */
}

.autocomplete-suggestions li {
  padding: 8px 12px;
  cursor: pointer;
  font-size: medium;
  color: #333;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.autocomplete-suggestions li:hover {
  background-color: rgba(0, 86, 157, 0.1);
  color: #00569d;
}